*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar{
  height: 70px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: black;
}

.links{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 600px;

}

.site-name{
  font-size: 30px;
  font-family: Hack,monospace;
  font-weight: 800;
  color: red;
}

.nav-ele{
  text-decoration: none;
  color: white;
  font-size: 20px;
  font-family: Hack,monospace;
  font-weight: 600;
}

.img-cont{
  margin-left: 180px;
  margin-top: 50px;
  width: 80%;
  height: 80vh;
  margin-bottom: 50px;
}

.img-cont img{
  height: 100%;
  width: 100%;
}

.home-title{
  font-size: 30px;
  font-family: hack,monospace;
  margin-top: 50px;
}

.item{
  height: 600px;
  width: 300px; 
  display: inline-block;
  margin: 30px;
  border: 2px solid black;
}

.img-container{
  height: 70%;
  width: 100%;
}

.img-container img{
  height: 100%;
  width: auto;
}

.btn{
  margin-top: 20px;
  background-color: orange;
  border-radius: 5px;
}

.btns{
  margin-top: 20px;
  width: 50px;
  background-color: orange;
  border-radius: 5px;
}

.cart-empty {
  font-family: monospace;
  position: absolute;
  top: 500px;
  left: 800px;
}
.title{
  margin-top: 50px;
  font-family: monospace;
}